<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process119"></a>

<p><br></p>
<p><span class="rvts17">找到埃米（</span><span class="rvts50">Finding Emmie</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:209,Y:103</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：巴迪（</span><span class="rvts20">Buddy</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">寻找巴迪的小伙伴埃米（</span><span class="rvts20">Emmie</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1033.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在欢乐堡贫民洞口附近，会看到一只来回溜达的狗，他叫做巴迪。</span>
<br><span class="rvts21">队伍中需要有一名拥有</span><span class="rvts20"></span><span class="rvts21">动物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">天赋的队友，可以和它进行交谈，并尽可能逗它开心。之后，它会在不远处刨坑，坑里有一把欢乐堡监狱驯兽房的钥匙。需要戴上这把钥匙，寻找驯兽房里的埃米。</span>
<br><span class="rvts21">需要注意，如果此时队伍中有那只小黑猫，那么这只狗就会以讨厌猫的理由而拒绝和队员交谈。需要拆分队伍，并使黑猫尽可能的离巴迪远一些。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1034.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">驯兽房的位置位于监狱区与犯人折磨区的交界处（坐标位置</span><span class="rvts20">X:346,Y:656</span><span class="rvts21">）。首先需要击败门外的训狗大师（</span><span class="rvts20">Magister Houndmaster</span><span class="rvts21">）拿得一个红色小球，然后使用钥匙，可以打开驯兽房的大门。</span>
<br><span class="rvts21">进入驯兽房，可以通过与埃米的友好交谈，以及使用红色小球，使得所有训犬变得中立。完成该任务，并同时与这个屋子里的所有战犬达成友好状态。这有利于进行附近其它房间的搜索，避免一场恶战。</span>
<br><span class="rvts21">需要注意的是，一旦逃离了欢乐堡，这里的所有战犬将再次与成为敌对状态。找到埃米并说明来由后，任务就算完成并自动关闭。即便再次拐回去告诉巴迪，依然没有任何其它奖励。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



